@import './variables.scss';

*, *::after, *::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;

  font-family: $Inter;

  scroll-behavior: smooth;
  transition: all .2s ease-in-out;
}

button {
  border: none;
  
  background: none;
  outline: none;
}

ul {
  list-style: none;
}